import _ from 'lodash';
import { Contract, Event, EventFilter, utils, BigNumber } from 'ethers';
import { toast } from 'react-toastify';
import { CHUNK_DATA_NUMBER } from '../config/appConfig';
import {
  BUSD_BEP20,
  USDT_BEP20,
  USDC_BEP20,
  BNB_BEP20,
} from '../config/tokenBep20Config';
import {
  PANCAKE_FACTORY,
  APE_FACTORY,
  BNB_BUSD_PANCAKE_POOL,
} from '../config/web3Config';
import tokenHelper from '../helper/tokenHelper';
import {
  bscProvider,
  bep20ProxyContract,
  getBep20Contract,
} from './web3Service';
import { dispatch, getState } from '../store/config/store';
import quoteTokenAction from '../store/quoteTokenActionCreater';
import tokenAction from '../store/tokenActionCreater';
import { DApp, IPool, ITokenDetail } from '../model/tokenModel';

type TTokenReserve = { [key: string]: [BigNumber, BigNumber] };

export async function updateTokenPrice() {
  const tokens: ITokenDetail[] = getState().tokens;
  const newTokens: ITokenDetail[] = await getTokenPrice(tokens);

  dispatch(tokenAction.updateTokenPrice(newTokens));
}

export async function getTokenPrice(
  tokens: ITokenDetail[],
  dApp?: DApp
): Promise<ITokenDetail[]> {
  if (_.size(tokens) === 0) {
    return tokens;
  }

  let newTokens: ITokenDetail[];
  let pancakeAdressPairs: [string, string][] = [];
  let apeAdressPairs: [string, string][] = [];

  tokens.forEach((token: ITokenDetail) => {
    switch (dApp || token.targetPool?.dApp) {
      case DApp.PANCAKE:
        pancakeAdressPairs.push([
          token.targetPool?.quoteAddress as string,
          token.address,
        ]);
        break;

      case DApp.APE:
        apeAdressPairs.push([
          token.targetPool?.quoteAddress as string,
          token.address,
        ]);
        break;
    }
  });

  let poolReserve: [[BigNumber, BigNumber][], [BigNumber, BigNumber][]] = [
    [],
    [],
  ];
  let isRequestError = false;

  try {
    poolReserve = await updateChunkTokenPrice(
      pancakeAdressPairs,
      apeAdressPairs,
      dApp
    );
  } catch (error) {
    isRequestError = true;
    toastUpdateTokenPriceError();
    console.log(error);
  }

  if (isRequestError) {
    return tokens;
  }

  const bnbReservePrice = tokenHelper.computedTokenReservePricePerQuote(
    _.last(poolReserve[0]) as [BigNumber, BigNumber]
  );
  dispatch(quoteTokenAction.updateBnb({ bnb: { price: bnbReservePrice } }));

  const tokenReserve: TTokenReserve = mappingTokeReserve(
    [...pancakeAdressPairs, ...apeAdressPairs],
    [...poolReserve[0].slice(0, poolReserve[0].length - 1), ...poolReserve[1]]
  );

  newTokens = tokens.map((token: ITokenDetail): ITokenDetail => {
    const reserve = tokenReserve[token.address];
    const newReserve: [string, string] = [
      utils.formatEther(reserve[0]),
      utils.formatUnits(reserve[1], token.decimals),
    ];
    let liquidity: number = token.targetPool?.liquidity as number;
    let reservePrice: number = token.price as number;

    switch (token.targetPool?.quoteAddress) {
      case BUSD_BEP20.address:
      case USDT_BEP20.address:
      case USDC_BEP20.address:
        liquidity = tokenHelper.computedLiquidity(reserve);
        reservePrice = tokenHelper.computedTokenReservePricePerQuote(
          reserve,
          token.decimals
        );

        break;

      case BNB_BEP20.address:
        liquidity = tokenHelper.computedLiquidity(reserve) * bnbReservePrice;
        reservePrice =
          tokenHelper.computedTokenReservePricePerQuote(
            reserve,
            token.decimals
          ) * bnbReservePrice;

        break;
    }

    return {
      ...token,
      price: reservePrice,
      targetPool: {
        ...(token.targetPool as IPool),
        liquidity,
        reservePrice,
        reserve: newReserve,
      },
    };
  });

  return newTokens;
}

async function updateChunkTokenPrice(
  pancakeAdressPairs: [string, string][],
  apeAdressPairs: [string, string][],
  dApp?: DApp
): Promise<[[BigNumber, BigNumber][], [BigNumber, BigNumber][]]> {
  const isQuotePancake = !dApp || dApp === DApp.PANCAKE;
  const chunkPancakeAddress: [string, string][][] = _.chunk(
    isQuotePancake
      ? [...pancakeAdressPairs, [BUSD_BEP20.address, BNB_BEP20.address]]
      : pancakeAdressPairs,
    CHUNK_DATA_NUMBER
  );
  const chunkApeAddress: [string, string][][] = _.chunk(
    !isQuotePancake
      ? [...apeAdressPairs, [BUSD_BEP20.address, BNB_BEP20.address]]
      : apeAdressPairs,
    CHUNK_DATA_NUMBER
  );
  let chunkPoolReservePancakePromises: Promise<any>[] = [];
  let chunkPoolReserveApePromises: Promise<any>[] = [];

  _.each(chunkPancakeAddress, (address: [string, string][]) => {
    chunkPoolReservePancakePromises.push(
      bep20ProxyContract.getReversesMulti(PANCAKE_FACTORY, address)
    );
  });
  _.each(chunkApeAddress, (address: [string, string][]) => {
    chunkPoolReserveApePromises.push(
      bep20ProxyContract.getReversesMulti(APE_FACTORY, address)
    );
  });

  const chunkDatas = await Promise.all([
    ...chunkPoolReservePancakePromises,
    ...chunkPoolReserveApePromises,
  ]);
  const poolReserve: [[BigNumber, BigNumber][], [BigNumber, BigNumber][]] =
    isQuotePancake
      ? [
          _.flatten(
            _.slice(chunkDatas, 0, _.size(chunkPoolReservePancakePromises)) as [
              BigNumber,
              BigNumber
            ][][]
          ),
          _.flatten(
            _.slice(
              chunkDatas,
              _.size(chunkPoolReservePancakePromises),
              _.size(chunkDatas)
            ) as [BigNumber, BigNumber][][]
          ),
        ]
      : [
          [
            ..._.flatten(
              _.slice(
                chunkDatas,
                0,
                _.size(chunkPoolReservePancakePromises)
              ) as [BigNumber, BigNumber][][]
            ),
            _.last(_.last(chunkDatas)) as [BigNumber, BigNumber],
          ],
          _.slice(
            _.flatten(
              _.slice(
                chunkDatas,
                _.size(chunkPoolReservePancakePromises),
                _.size(chunkDatas)
              ) as [BigNumber, BigNumber][][]
            ),
            0,
            _.size(apeAdressPairs)
          ),
        ];

  return poolReserve;
}

function mappingTokeReserve(
  addressPairs: [string, string][],
  reserve: [BigNumber, BigNumber][]
): TTokenReserve {
  const tokenReserve: TTokenReserve = {};

  addressPairs.forEach((address: [string, string], index: number) => {
    tokenReserve[address[1]] = reserve[index];
  });

  return tokenReserve;
}

let timer = 0;
function toastUpdateTokenPriceError() {
  if (timer === 0) {
    toast.error('Update token price failure!', {
      autoClose: 1000,
      hideProgressBar: true,
    });
    timer = +new Date();
  } else if (+new Date() >= timer + 3 * 1000) {
    timer = 0;
  }
}

// Legacy code
type TSwapAmount = {
  amount0In: BigNumber;
  amount0Out: BigNumber;
  amount1In: BigNumber;
  amount1Out: BigNumber;
};

export async function updateTokenOldPrice() {
  const OLD_BLOCK = 400;
  const RANGE_BLOCK = 60;
  const tokens: ITokenDetail[] = getState().tokens;
  const bnbToken: ITokenDetail = {
    ...BNB_BEP20,
    targetPool: {
      address: BNB_BUSD_PANCAKE_POOL,
      quoteSymbol: BUSD_BEP20.symbol as string,
      quoteAddress: BUSD_BEP20.address,
    },
  };
  const lastBlock: number = await bscProvider.getBlockNumber();
  let oldSwapPromises: Promise<ITokenDetail | null>[] = [];

  [...tokens, bnbToken].forEach((token: ITokenDetail) => {
    const poolContract: Contract = getBep20Contract(
      token.targetPool?.address as string
    );
    const filter: EventFilter = poolContract.filters.Swap();
    const oldSwapPromise = poolContract
      .queryFilter(
        filter,
        lastBlock - OLD_BLOCK - RANGE_BLOCK,
        lastBlock - OLD_BLOCK
      )
      .then((results: Event[]): ITokenDetail | null => {
        if (results.length === 0) {
          return null;
        }

        const swapAmount: TSwapAmount = {
          amount0In: _.last(results)?.args?.['amount0In'],
          amount0Out: _.last(results)?.args?.['amount0Out'],
          amount1In: _.last(results)?.args?.['amount1In'],
          amount1Out: _.last(results)?.args?.['amount1Out'],
        };

        return {
          ...token,
          oldPrice: Math.abs(
            computedTokenSwapPricePerQuote(
              token.targetPool?.quoteAddress as string,
              token.address,
              token.decimals as number,
              swapAmount
            )
          ),
        };
      });

    oldSwapPromises.push(oldSwapPromise);
  });

  let oldSwapTokens: ITokenDetail[] = _.compact(
    await Promise.all(oldSwapPromises)
  );

  oldSwapTokens = convertOldPriceToUsd(oldSwapTokens);

  dispatch(tokenAction.updateTokenOldPrice(oldSwapTokens));
}

function computedTokenSwapPricePerQuote(
  quoteTokenAddress: string,
  tokenAddress: string,
  tokenDecimals: number,
  swapAmount: TSwapAmount
): number {
  const quoteTokenBigNumber = BigNumber.from(quoteTokenAddress);
  const tokenBigNumber = BigNumber.from(tokenAddress);

  let swapPrice: number;

  if (quoteTokenBigNumber.lt(tokenBigNumber)) {
    const amount0In = Number(utils.formatEther(swapAmount.amount0In));
    const amount0Out = Number(utils.formatEther(swapAmount.amount0Out));
    const amount1In = Number(
      utils.formatUnits(swapAmount.amount1In, tokenDecimals)
    );
    const amount1Out = Number(
      utils.formatUnits(swapAmount.amount1Out, tokenDecimals)
    );

    if (amount0In) {
      swapPrice = amount0In / amount1Out;
    } else {
      swapPrice = -amount0Out / amount1In;
    }
  } else {
    const amount0In = Number(
      utils.formatUnits(swapAmount.amount0In, tokenDecimals)
    );
    const amount0Out = Number(
      utils.formatUnits(swapAmount.amount0Out, tokenDecimals)
    );
    const amount1In = Number(utils.formatEther(swapAmount.amount1In));
    const amount1Out = Number(utils.formatEther(swapAmount.amount1Out));

    if (amount0In) {
      swapPrice = -amount1Out / amount0In;
    } else {
      swapPrice = amount1In / amount0Out;
    }
  }

  return swapPrice;
}

function convertOldPriceToUsd(oldSwapTokens: ITokenDetail[]): ITokenDetail[] {
  const oldPriceBnb: number = _.last(oldSwapTokens)?.oldPrice as number;

  return oldSwapTokens.map((token: ITokenDetail): ITokenDetail => {
    if (BNB_BEP20.address === token.targetPool?.quoteAddress) {
      return {
        ...token,
        oldPrice: (token.oldPrice as number) * (oldPriceBnb as number),
      };
    }

    return token;
  });
}
